.subscribe {
  margin: 0 auto;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    transform: translateY(-43px);
  }
  @include tablet {
    transform: translateY(-74px);
  }
  @include desktop {
    transform: translateY(-66px);
  }
}

.subscribe-wrap {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 60px;
  }
  @include tablet {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
  }
  @include desktop {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
  }
}

.subscribe-img {
  @include mobile {
    width: 161px;
  }
  @include tablet {
    width: 281px;
  }
  @include desktop {
    width: 608px;
  }
}

.subscribe-title {
  margin-top: 17px;
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.21;
  color: #2c2c2c;
  @include tablet {
    margin: 20px 0;
    font-size: 42px;
    line-height: 1.19;
  }
  @include desktop {
    margin-bottom: 20px;
  }
}

.subscribe-text {
  margin-bottom: 40px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  color: #2c2c2c;
  @include tablet {
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 1.5;
  }
}

.subscribe-form {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  @include tablet {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    gap: 22px;
    margin-bottom: 100px;
  }
  @include desktop {
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-start;
    gap: 32px;
  }
}

.subscribe-input {
  padding-left: 11px;
  width: 278px;
  height: 42px;
  background: #ffffff;
  border: 1px solid #ffe550;
  border-radius: 30px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;
  color: #505050;
  outline: transparent;
  @include tablet {
    width: 394px;
  }
  @include desktop {
    width: 394px;
  }
}

.subscribe-input:hover {
  border: 1px solid #ffe550;
}

.subscribe-input:not(:placeholder-shown):not(:focus):valid {
  border: 1px solid #7eb101;
  border-radius: 30px;
}

.subscribe-input:not(:placeholder-shown):not(:focus):invalid {
  border: 1px solid #ff705d;
  border-radius: 30px;
}

.subscribe-input:not(:placeholder-shown):not(:focus):invalid
  + .subscribe-invalid {
  display: block;
}

.subscribe-invalid {
  display: none;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.5;
  color: #ff705d;
}

.subscribe-button {
  margin-left: auto;
  min-width: 103px;
  height: 42px;
  color: #2c2c2c;
  border: none;
  cursor: pointer;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.5;
  @include tablet {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
  }
  @include desktop {
    margin: 0;
  }
}
